@import '~assets/styles/h3Imports';

.add-meds-card {
  align-items: center;
  background-color: var(--white);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  display: flex;
  flex-direction: row;
  margin-bottom: 16px;
  padding: 16px;
  width: 100%;

  figure {
    flex: 0 1 rem(48);
    max-width: rem(48);
    width: 100%;
  }
}

.card-container {
  align-items: center;
  display: flex;
  @media #{$large-up} {
    width: 80%;
  }
}

.title {
  @include h5;

  flex: 0 1 auto;
  margin: 16px;
}

.add-meds-button {
  flex: 0 1 rem(110);
  font-size: rem(16);
  height: min-content;
  margin-left: auto;
  min-width: rem(110);
  padding: 0;
  width: rem(110);
}

@media #{$tablet-down} {
  .add-meds-buttons {
    max-width: rem(120);
    min-width: rem(120);
  }
}

@media #{$small-down} {
  .add-meds-card {
    align-items: flex-start;
    flex-direction: column;
    padding: 16px;
  }

  .add-meds-button {
    flex: 0 1 auto;
    margin-left: 0;
    margin-top: 16px;
  }
}
